import { HashRouter, Switch, Route } from "react-router-dom";
import React, { Component } from 'react'
import Navbar from './components/navbar'
import CountryList from './components/menu'
import {Main, Header} from './components/content'
import MostInfectedTable from './components/table'
import Canvas from './components/canvas'
import Progress from './components/progress'

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            countries: [],
        }
    }
    componentDidMount() {
        fetch("https://pomber.github.io/covid19/timeseries.json")
        .then(response => response.json())
        .then(
            data => { 
                this.setState({
                    countries: Object.keys(data)
                })
            }
        )
    }
    render() {
        const { countries} = this.state
        return (
            <HashRouter basename='/covid-19/'>
                <Navbar> Covid-19 </Navbar> 
                <div className="container-fluid">
                    <div className="row">
                        <CountryList/>
                        <Main>
                            <Switch>
                                <Route exact path="/"  component = {AllStatistics} />
                                {countries.map(url => {
                                    return (
                                        <Route path={`/${url}`} component={() => {
                                           return <Countries country={url}/>
                                        }}/>
                                    )
                                })}
                            </Switch>
                        </Main>
                    </div>
                </div>
            </HashRouter>
        )
    }
}

class AllStatistics extends Component {
    render() {
        return (
            <div>
                <Header> Statistics </Header>
                <Canvas id = "myChart" width = {900} height = {380} />
                <h2>Most infected</h2>
                <MostInfectedTable/>
            </div>
        )
    }
}

class Countries extends Component {
    render() {
        return (
            <div>
                <Header> 
                    Statistics in {this.props.country}
                </Header>
                <Canvas id = "myChart" width = {900} height = {380} />
                <h2> Coronavirus progress </h2>
                <Progress country={this.props.country}/>
            </div>
        )
    }
}

export default App